#hero-req-quote {
    width: 100%;
    position: relative;
    height: 110vh;
    background-size: cover;
    background-position: center;
    object-fit: cover;
    object-position: center;

    .navbar {
        display: none;
    }

    .logo {
        padding-top: 19px;
        padding-bottom: 19px;
        font-family: "Poppins";
        font-weight: 700;
        font-size: 28px;
        line-height: 42px;
        letter-spacing: -0.04em;
        color: #ffffff;
    }

    #hero {
        padding-top: 50px;

        .top {
            padding-left: 10px;
            padding-right: 10px;
            width: max-content;

            h1 {
                font-family: "Champ";
                font-weight: 900;
                font-size: 56px;
                line-height: 64px;
                text-align: center;
                letter-spacing: 0.02em;
                color: #ffffff;
                margin-bottom: 24px;
            }

            span {
                font-family: "Poppins";
                font-weight: 400;
                font-size: 20px;
                line-height: 32px;
                text-align: center;
                letter-spacing: 0.01em;
                color: #ffffff;
            }
        }
    }
}

#req-quote {
    padding-top: 100px;
    padding-bottom: 100px;

    form {
        padding-left: 50px;
        padding-right: 50px;

        h3 {
            padding-bottom: 30px;
            font-family: "Poppins";
            font-weight: 600;
            font-size: 24px;
            line-height: 36px;
            color: #10121f;
        }

        h4 {
            margin-top: 30px;
            font-weight: 600;
            font-size: 22px;
            line-height: 33px;
            color: #10121f;
        }

        .what {
            .mobile {
                display: none;
            }

            .form-group {
                margin-top: 30px;
                font-family: "Poppins";

                .d-flex {
                    margin-bottom: 20px;

                    &:last-child() {
                        margin-bottom: 0;
                    }

                    .input-group {
                        width: max-content;

                        &:nth-child(2) {
                            margin-left: 16px;
                            margin-right: 16px;
                        }
                    }
                }

                .btn-check:checked + .btn {
                    background: #613ad2;
                    color: #ffffff;
                }

                .btn-outline-secondary {
                    padding: 14px 18px;
                    border: 1px solid #a29cb0;
                    border-radius: 40px;
                    font-weight: 700;
                    font-size: 18px;
                    line-height: 25px;
                    letter-spacing: 0.03em;
                    color: #10121f;
                }
            }
        }

        .tell {
            padding-top: 60px;

            .form-control {
                outline: none;
                background: #ffffff;
                border: 1px solid #cfcdd5;
                border-radius: 30px;
                padding: 16px 20px;
            }
        }

        .describe {
            padding-top: 60px;

            .form-control {
                outline: none;
                background: #ffffff;
                border: 1px solid #cfcdd5;
                border-radius: 20px;
                padding: 16px 20px;
            }
        }

        .package {
            padding-top: 60px;

            .mobile {
                display: none;
            }

            h3 {
                padding-bottom: 0;
                margin-bottom: 0;
            }

            h4 {
                text-align: start;
                font-family: "Poppins";
                font-weight: 400;
                font-size: 20px;
                line-height: 32px;
                letter-spacing: 0.01em;
                color: #10121f;
                margin-top: 8px;

                 a {
                    color: #613AD2;
                    text-decoration: underline;
                 }
            }

            .form-group {
                font-family: "Poppins";

                .d-flex {
                    &:last-child() {
                        margin-bottom: 0;
                    }

                    .input-group {
                        width: max-content;

                        &:nth-child(2),
                        &:nth-child(3),
                        &:nth-child(4) {
                            margin-left: 16px;
                        }
                    }
                }

                .btn-check:checked + .btn {
                    background: #613ad2;
                    color: #ffffff;
                }

                .btn-outline-secondary {
                    padding: 14px 18px;
                    border: 1px solid #a29cb0;
                    border-radius: 40px;
                    font-weight: 700;
                    font-size: 18px;
                    line-height: 25px;
                    letter-spacing: 0.03em;
                    color: #10121f;
                }
            }
        }

        .submit {
            .col-sm-12 {
                text-align: end;

                .btn-submit {
                    margin-top: 56px;
                    background: #1a0e3c;
                    border-radius: 40px;
                    padding: 16px 25px;
                    font-family: "Poppins";
                    font-weight: 700;
                    font-size: 18px;
                    line-height: 27px;
                    letter-spacing: 0.02em;
                    color: #ffffff;
                }
            }
        }
    }

}

#look {
    padding-top: 120px;
    padding-bottom: 95px;
    background-color:  #F7F9FF;

    .left {
        .top {
            display: none;
        }

        .title {
            h1 {
                font-family: 'Champ';
                font-weight: 900;
                font-size: 48px;
                line-height: 60px;
                letter-spacing: 0.02em;
                color: #10121F;
            }

            .mobile {
                display: none;
            }
        }

        .bottom {
            position: absolute;

            img {
                z-index: -1;
                margin-top: -10px;
                margin-left: 150px;
            }
        }

        p {
            font-family: 'Poppins';
            font-weight: 400;
            font-size: 20px;
            line-height: 32px;
            letter-spacing: 0.01em;
            color: #10121F;
            padding-top: 30px;
            padding-bottom: 30px;
        }

        .btn-say-hello {
            padding: 16px 25px;
            font-family: 'Poppins';
            font-weight: 700;
            font-size: 18px;
            line-height: 27px;
            letter-spacing: 0.02em;
            text-decoration-line: underline;
            color: #613AD2;
            border: 1px solid #613AD2;
            border-radius: 40px;
        }
    }

    .accordion-item {
        border: none;

        .accordion-button {
            box-shadow: none;
            border-top: none;
            border-right: none;
            border-left: none;
            padding: 24px 0;
            font-family: 'Poppins';
            font-weight: 600;
            font-size: 18px;
            line-height: 26px;
            color: #18162D;
            border-bottom: 1px solid #CFCDD4;
            transition: .3s ease-in;

            &:not(.collapsed) {
                border-bottom: none;
                transition: .3s ease-out;

                &::after {
                    background-image: url("../img/arrow-circle-up.png");
                    transform: rotate(360deg);
                }
            }

            &:focus {
                z-index: 3;
                border-color: none;
                outline: 0;
                box-shadow: none;
            }

            &::after {
                flex-shrink: 0;
                width: 32px;
                height: 32px;
                margin-left: auto;
                content: "";
                background-image: url('../img/arrow-circle-down.png');
                background-repeat: no-repeat;
                background-size: 32px;
                transition: transform 0.2s ease-in-out;
            }
        }

        .accordion-body {
            padding-top: 8px;
            padding-bottom: 20px;
            font-family: 'Poppins';
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            letter-spacing: 0.01em;
            color: #676977;
            border-bottom: 1px solid #CFCDD4;
            transition: .3s ease-in-out;
        }
    }

    .bottom-faq {
        display: none;
    }
}

