                    line-height: 27px;
                }

                .form-group  {
                    margin-top: 0;

                    .btn-outline-secondary {
                        font-size: 16px;
                        line-height: 24px;
                    }
                }
            }

            .tell {
                padding-top: 30px;

                h3 {
                    font-size: 20px;
                    line-height: 30px;
                    padding-bottom: 24px;
                    margin-bottom: 0;
                }

                .form-group {
                    margin-bottom: 16px;
                }

                .col-md-10 {
                    .row {
                        &:last-child {
                            margin-top: 0 !important;
                        }
                    }
                }
            }

            .describe {
                padding-top: 34px;

                h3 {
                    font-size: 20px;
                    line-height: 30px;
                    padding-bottom: 24px;
                    margin-bottom: 0;
                }
            }

            .package {
                padding-top: 50px;

                .desktop {
                    display: none;
                }

                .mobile {
                    display: block;

                    .d-flex {
                        &:last-child {
                            margin-top: 16px;
                        }
                    }
                }

                h3 {
                    font-size: 20px;
                    line-height: 30px;
                }

                h4 {
                    margin-top: 10px;
                    margin-bottom: 24px;
                    font-size: 16px;
                    line-height: 24px;
                }
            }

            .submit {
                .col-sm-12 {
                    .btn-submit {
                        width: 100% ;
                        margin-top: 40px;
                    }
                }
            }
        }
    }

    #look {
        padding-top: 30px;
        padding-bottom: 50px;

        .left {
            .title {
                h1 {
                    padding-top: 32px;
                    font-size: 34px;
                    line-height: 40px;
                }
            }

            p {
                display: none;
                font-size: 15px;
                line-height: 24px;
                margin-bottom: 0;
                padding-top: 24px;
                padding-bottom: 40px;
            }

            a {
                display: block;
            }

            .top {
                margin-top: -20px;
                // display: flex;
            }

            .bottom {
                img {
                    margin-left: 0;
                }
            }
        }

        .desktop {
            display: block;
        }

        .mobile {
            display: none;
        }

        .accordion-item {
            .accordion-button {
                font-size: 17px;
            }
        }

        .bottom-faq {
            padding-top: 40px;
            display: block;

            p {
                font-family: 'Poppins';
                font-weight: 400;
                font-size: 16px;
                line-height: 24px;
                letter-spacing: 0.01em;
                color: #10121F;
                margin-bottom: 0;
                padding-top: 24px;
                padding-bottom: 40px;
            }

            a {
                padding: 16px 25px;
                font-family: 'Poppins';
                font-weight: 700;
                font-size: 18px;
                line-height: 27px;
                letter-spacing: 0.02em;
                text-decoration-line: underline;
                color: #613AD2;
                border: 1px solid #613AD2;
                border-radius: 40px;
            }
        }
    }
}

